import React, { useCallback, useEffect, useRef, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import './css/index.css';
import MouseOverPopover from './components/MouseOverPopover';
import ShowScore from './components/ShowScore';
import SentimentSatisfiedOutlinedIcon from '@mui/icons-material/SentimentSatisfiedOutlined';
import SentimentDissatisfiedOutlinedIcon from '@mui/icons-material/SentimentDissatisfiedOutlined';
import { useDispatch } from 'react-redux';


function Game() {
  const navigate = useNavigate();
  const dispatch = useDispatch(); // Result   
  const numOfProbs = 10;

  const [text, setText] = useState('');
  const [showResult, setShowResult] = useState(false);
  const [correctAnswer, setCorrectAnswer] = useState(true);
  const [display, setDisplay] = useState({}); //  
  const [gus, setGus] = useState([]); //    
  const inputRef = useRef();

  const items = [
    {
      name: '63',
      src: "assets/game/63.jpeg",
      answer: ''
    },
    {
      name: '',
      src: "assets/game/.jpeg",
      answer: ''
    },
    {
      name: '',
      src: "assets/game/.jpeg",
      answer: ''
    },
    {
      name: '()',
      src: "assets/game/.jpeg",
      answer: ''
    },
    {
      name: '',
      src: "assets/game/.jpeg",
      answer: ''
    },
    {
      name: '',
      src: "assets/game/.jpeg",
      answer: ''
    },
    {
      name: '',
      src: "assets/game/.jpeg",
      answer: ''
    },
    {
      name: '',
      src: "assets/game/.jpeg",
      answer: ''
    },
    {
      name: ' LG',
      src: "assets/game/LG.jpeg",
      answer: ''
    },
    {
      name: '',
      src: "assets/game/.jpeg",
      answer: ''
    },
    {
      name: '',
      src: "assets/game/.jpeg",
      answer: ''
    },
    {
      name: '',
      src: "assets/game/.jpeg",
      answer: ''
    },
    {
      name: 'MBC',
      src: "assets/game/MBC.jpeg",
      answer: ''
    },
    {
      name: '(  )',
      src: "assets/game/.jpeg",
      answer: ''
    },
    {
      name: '',
      src: "assets/game/.jpeg",
      answer: ''
    },
    {
      name: '',
      src: "assets/game/.jpeg",
      answer: ''
    },
    {
      name: '',
      src: "assets/game/.jpeg",
      answer: ''
    },
    {
      name: '',
      src: "assets/game/.jpeg",
      answer: ''
    },
    {
      name: '',
      src: "assets/game/.jpeg",
      answer: ''
    },
    {
      name: '',
      src: "assets/game/.jpeg",
      answer: ''
    },
    {
      name: '',
      src: "assets/game/.jpeg",
      answer: ''
    },
    {
      name: '',
      src: "assets/game/.jpeg",
      answer: ''
    },
    {
      name: '',
      src: "assets/game/.jpeg",
      answer: ''
    },
    {
      name: 'DDP()',
      src: "assets/game/DDP.jpeg",
      answer: ''
    },
    {
      name: 'N()',
      src: "assets/game/N.jpeg",
      answer: ''
    },
    // , , , , , 
  ]

  /* useEffect           ,
       
  */
  useEffect(() => {
    //  
    items.sort(() => Math.random() - 0.5);
    setDisplay(items[0]);
    setGus(items.slice(1, numOfProbs));
    dispatch({
      type : 'START'
    });
  }, [])

  const __goResult = useCallback(
    () => {
      navigate('/result');
    },
    [navigate],
  )

  const __doSubmit = useCallback(
    (e) => {
      e.preventDefault(); //  

      if (text === display.answer) {
        setCorrectAnswer(true);
        dispatch({
          type : 'CORRECT'
        });
      } else {
        setCorrectAnswer(false);
      }
      setShowResult(true); //  

      setTimeout(() => {
        setShowResult(false); //   
        setText('');  //  
        inputRef.current.focus();

        if(gus.length) {
          const _gus = gus.slice(1,numOfProbs + 1);
          setDisplay(gus[0]);
          setGus(_gus); 
        } else {
          __goResult();
        }
      }, 1000);
      console.log(`gus length: ${gus.length}`);
    }, [text, display, gus, dispatch, __goResult]
  );


  return (
    <div className='game'>
      <div className='wrapper'>
        <img className='gu-img' src={display.src} alt='' />
        <div className="score-container">
          <ShowScore />
        </div>
        <span className="map-icon">
          <MouseOverPopover />
        </span>
        <div className='quiz-container'>
          <div className='gu-name'>{display.name}</div>
          {gus.length ? (
            <div className='hidden-hint'> {gus.length} .</div>
          ):(
            <div className='hidden-hint'> .</div>
          )
          }
          <div className='input-and-result'>
            {showResult ? ((correctAnswer ? (
                <div className="o-icon">
                  <audio src='assets/music/correct.mp3' autoPlay></audio>
                  <SentimentSatisfiedOutlinedIcon color="success" sx={{ fontSize: 100}} />
                </div>
              ):(
                <div className="x-icon">
                  <audio src='assets/music/wrong.mp3' autoPlay></audio>
                  <SentimentDissatisfiedOutlinedIcon sx={{ color:'red', fontSize: 100}} />
                </div>
              )
            )):(
              <form className='answer-container' onSubmit={__doSubmit}>
                <input type='text' placeholder=' ' value={text}
                  onChange={(e) => setText(e.target.value)} ref={inputRef} required />
                <button className='submit-btn' type='submit'></button>
              </form>
            )}
          </div>
        </div>
      </div>
    </div>
  );
}

export default Game;